import axios from "axios";
import { API_USER } from "../../utilities/constant";
import { handleApiResponse, handleApiError } from "../../utilities/handlerApi";

export const fetchCategories = async (auth) => {
  const { access_token, token_type } = auth;
  try {
    const response = await axios.get(`${API_USER}/category`, {
      headers: {
        Authorization: `${token_type} ${access_token}`,
      },
    });
    return handleApiResponse(response, "カテゴリの取得に失敗しました。");
  } catch (err) {
    handleApiError(err);
  }
};

export const deleteCategories = async (auth, data) => {
  const { access_token, token_type } = auth;
  try {
    const response = await axios.post(`${API_USER}/category/delete`, data, {
      headers: {
        Authorization: `${token_type} ${access_token}`,
      },
    });
    return handleApiResponse(
      response,
      "あるメニュー使用するからカテゴリの削除に失敗しました。"
    );
  } catch (err) {
    handleApiError(err);
  }
};

export const createCategory = async (auth, data) => {
  const { access_token, token_type } = auth;
  try {
    const response = await axios.post(`${API_USER}/category/create`, data, {
      headers: {
        Authorization: `${token_type} ${access_token}`,
      },
    });
    return handleApiResponse(response, "メニューの作成に失敗しました。");
  } catch (err) {
    handleApiError(err);
  }
};

export const updateCategory = async (auth, menuId, data) => {
  const { access_token, token_type } = auth;
  try {
    const response = await axios.put(
      `${API_USER}/category/update/${menuId}`,
      data,
      {
        headers: {
          Authorization: `${token_type} ${access_token}`,
        },
      }
    );
    return handleApiResponse(response, "メニューの更新に失敗しました。");
  } catch (err) {
    handleApiError(err);
  }
};
