import React, { useState, useEffect } from "react";
import {
  Box,
  TextField,
  Button,
  Typography,
  Select,
  MenuItem,
  IconButton,
  Grid,
  FormControlLabel,
  Checkbox,
} from "@mui/material";
import AddIcon from "@mui/icons-material/Add";
import RemoveIcon from "@mui/icons-material/Remove";
import { fetchCategories } from "../../actions/CallApi/CategoryFetch";
import { useAuth } from "../../context/AuthContext";

const MenuForm = ({
  formData,
  onChange,
  onSubmit,
  handleCancel,
  isSubmitting,
  submitLabel,
}) => {
  const [imagePreview, setImagePreview] = useState("");
  const [categories, setCategories] = useState([]);
  const [sizes, setSizes] = useState(formData.sizes || []);
  const [errors, setErrors] = useState({});
  const [sizeErrors, setSizeErrors] = useState([]);
  const { callApiWithToken } = useAuth();

  const initialCategoryId = formData.category_id || formData.category?.id || "";

  useEffect(() => {
    setImagePreview(formData.image_url || "");
  }, [formData.image_url]);

  useEffect(() => {
    setSizes(formData.sizes || []);
  }, [formData.sizes]);

  useEffect(() => {
    const fetchData = async () => {
      const data = await callApiWithToken(fetchCategories);
      setCategories(data);
    };

    fetchData();
  }, [callApiWithToken]);

  const handleFileChange = (e) => {
    const file = e.target.files[0];
    if (file) {
      const reader = new FileReader();
      reader.onloadend = () => {
        setImagePreview(reader.result);
        onChange({ target: { name: "image_url", value: reader.result } });
      };
      reader.readAsDataURL(file);
    }
  };

  const handleSizeChange = (index, field, value) => {
    const updatedSizes = [...sizes];
    updatedSizes[index][field] = value;
    setSizes(updatedSizes);
    onChange({ target: { name: "sizes", value: updatedSizes } });
  };

  const addSize = () => {
    const newSizes = [...sizes, { size: "", price: "", description: "" }];
    setSizes(newSizes);
    onChange({ target: { name: "sizes", value: newSizes } });
    setSizeErrors([...sizeErrors, {}]);
  };

  const removeSize = (index) => {
    const newSizes = sizes.filter((_, i) => i !== index);
    setSizes(newSizes);
    onChange({ target: { name: "sizes", value: newSizes } });

    const newSizeErrors = sizeErrors.filter((_, i) => i !== index);
    setSizeErrors(newSizeErrors);
  };

  const handleFormSubmit = () => {
    let validationErrors = {};
    let validationSizeErrors = [];

    // Validate các field chính
    if (!formData.name.trim())
      validationErrors.name = "料理名は必須です";
    if (!formData.description.trim())
      validationErrors.description = "説明は必須です";
    if (!formData.category_id)
      validationErrors.category_id = "カテゴリは必須です";

    // Validate sizes
    if (sizes.length === 0) {
      validationErrors.sizes = "サイズを少なくとも1つ選択してください";
    } else {
      sizes.forEach((s, idx) => {
        let err = {};
        if (!s.size.trim()) err.size = "サイズは必須で";
        if (!s.description.trim())
          err.description = "説明は必須です";
        if (s.price === "" || isNaN(s.price))
          err.price = "価格は数値で、必須項目です";
        validationSizeErrors[idx] = err;
      });
    }

    setErrors(validationErrors);
    setSizeErrors(validationSizeErrors);

    const hasSizeErrors = validationSizeErrors.some(
      (err) => Object.keys(err).length > 0
    );

    if (Object.keys(validationErrors).length === 0 && !hasSizeErrors) {
      onSubmit();
    }
  };

  return (
    <Box component="form" noValidate autoComplete="off">
      <TextField
        fullWidth
        label="名"
        name="name"
        value={formData.name}
        onChange={onChange}
        margin="normal"
        error={!!errors.name}
        helperText={errors.name || " "}
      />
      <TextField
        fullWidth
        label="説明"
        name="description"
        value={formData.description}
        onChange={onChange}
        margin="normal"
        multiline
        error={!!errors.description}
        helperText={errors.description || " "}
      />

      <Box marginY={2}>
        <Typography>カテゴリ:</Typography>
        <Select
          fullWidth
          value={initialCategoryId}
          onChange={(e) =>
            onChange({ target: { name: "category_id", value: e.target.value } })
          }
          name="category_id"
          error={!!errors.category_id}
        >
          {categories.map((cat) => (
            <MenuItem key={cat.id} value={cat.id}>
              {cat.category_name}
            </MenuItem>
          ))}
        </Select>
        {errors.category_id && (
          <Typography color="error" variant="body2" mt={0.5}>
            {errors.category_id}
          </Typography>
        )}
      </Box>

      <Box marginY={2}>
        <Typography>イメージ:</Typography>
        {imagePreview && (
          <img
            src={imagePreview}
            alt="preview"
            style={{
              width: 150,
              height: 150,
              objectFit: "cover",
              marginBottom: 8,
            }}
          />
        )}
        <input type="file" accept="image/*" onChange={handleFileChange} />
      </Box>

      <Box marginY={2}>
        <Typography>サイズ:</Typography>
        {errors.sizes && (
          <Typography color="error" variant="body2" mb={1}>
            {errors.sizes}
          </Typography>
        )}
        {sizes.map((s, idx) => (
          <Grid
            container
            spacing={1}
            alignItems="center"
            key={idx}
            marginBottom={1}
          >
            <Grid item xs={2}>
              <TextField
                label="サイズ"
                value={s.size}
                onChange={(e) => handleSizeChange(idx, "size", e.target.value)}
                error={!!sizeErrors[idx]?.size}
                helperText={sizeErrors[idx]?.size || " "}
              />
            </Grid>
            <Grid item xs={3}>
              <TextField
                label="価格"
                type="number"
                value={s.price}
                onChange={(e) =>
                  handleSizeChange(idx, "price", Number(e.target.value))
                }
                error={!!sizeErrors[idx]?.price}
                helperText={sizeErrors[idx]?.price || " "}
              />
            </Grid>
            <Grid item xs={5}>
              <TextField
                label="説明"
                value={s.description}
                onChange={(e) =>
                  handleSizeChange(idx, "description", e.target.value)
                }
                error={!!sizeErrors[idx]?.description}
                helperText={sizeErrors[idx]?.description || " "}
              />
            </Grid>
            <Grid item xs={2}>
              <IconButton onClick={() => removeSize(idx)}>
                <RemoveIcon />
              </IconButton>
            </Grid>
          </Grid>
        ))}
        <Button variant="outlined" startIcon={<AddIcon />} onClick={addSize}>
          追加サイズ
        </Button>
      </Box>

      <Box marginY={2}>
        <Typography>人気:</Typography>
        <FormControlLabel
          control={
            <Checkbox
              checked={!!formData.is_liked}
              onChange={(e) =>
                onChange({
                  target: { name: "is_liked", value: e.target.checked },
                })
              }
            />
          }
          label="人気"
        />
      </Box>

      <Box marginY={2} sx={{ display: "flex", gap: 1 }}>
        <Button
          variant="contained"
          color="primary"
          onClick={handleFormSubmit}
          disabled={isSubmitting}
          sx={{ textTransform: "none" }}
        >
          {submitLabel}
        </Button>

        <Button
          variant="contained"
          color="secondary"
          onClick={handleCancel}
          sx={{ textTransform: "none" }}
        >
          キャンセル
        </Button>
      </Box>
    </Box>
  );
};

export default MenuForm;
