import React, { useState, useEffect } from "react";
import { Box, Typography, Paper } from "@mui/material";
import { useNavigate, useLocation } from "react-router-dom";
import MenuForm from "./MenuForm";
import { createMenu, updateMenu } from "../../actions/CallApi/MenuFetch";
import { useAuth } from "../../context/AuthContext";

const MenuAddOrEditPage = () => {
  const navigate = useNavigate();
  const location = useLocation();
  const { callApiWithToken } = useAuth();
  const initialData = location.state?.initialData;

  const [formData, setFormData] = useState({
    name: "",
    description: "",
    category_id: "",
    is_liked: false,
    sizes: [],
    image_url: "",
  });

  const [isSubmitting, setIsSubmitting] = useState(false);
  const [errorMessage, setErrorMessage] = useState("");

  useEffect(() => {
    if (initialData) {
      const initialCategoryId = initialData.category_id || initialData.category?.id || "";
      setFormData({
        ...initialData,
        category_id: initialCategoryId,
      });
    }
  }, [initialData]);

  const handleChange = (e) => {
    const { name, value } = e.target;
    setFormData((prev) => ({ ...prev, [name]: value }));
  };

  const handleSubmit = async () => {
    setIsSubmitting(true);
    setErrorMessage("");
    try {
      if (initialData?.id) {
        await callApiWithToken(updateMenu, initialData.id, formData);
        navigate("/menu");
      } else {
        await callApiWithToken(createMenu, formData);
        navigate("/menu");
      }
    } catch (error) {
      setErrorMessage(error?.message || "API呼ぶとき発生した");
    } finally {
      setIsSubmitting(false);
    }
  };

  const handleCancel = () => navigate("/menu");

  return (
    <Box>
      <Typography variant="h4" gutterBottom>
        {initialData ? "✏️ 変更" : "➕ 追加"}
      </Typography>

      <Paper sx={{ p: 3 }}>
        <MenuForm 
          formData={formData}
          onChange={handleChange}
          onSubmit={handleSubmit}
          handleCancel={handleCancel}
          isSubmitting={isSubmitting}
          submitLabel={initialData ? "変更" : "追加"}
        />
        {errorMessage && (
          <Typography color="error" mt={2}>
            {errorMessage}
          </Typography>
        )}
      </Paper>
    </Box>
  );
};

export default MenuAddOrEditPage;
