import axios from "axios";
import { API_USER } from "../../utilities/constant";
import { handleApiResponse, handleApiError } from "../../utilities/handlerApi";

export const fetchMenu = async () => {
  try {
    const response = await axios.get(`${API_USER}/menu`);
    return handleApiResponse(response, "メニューの取得に失敗しました。");
  } catch (err) {
    handleApiError(err);
  }
};

export const createMenu = async (auth, data) => {
  const { access_token, token_type } = auth;
  try {
    const response = await axios.post(`${API_USER}/menu/create`, data, {
      headers: {
        Authorization: `${token_type} ${access_token}`,
      },
    });
    return handleApiResponse(response, "メニューの作成に失敗しました。");
  } catch (err) {
    handleApiError(err);
  }
};

export const updateMenu = async (auth, menuId, data) => {
  const { access_token, token_type } = auth;
  try {
    const response = await axios.put(
      `${API_USER}/menu/update/${menuId}`,
      data,
      {
        headers: {
          Authorization: `${token_type} ${access_token}`,
        },
      }
    );
    return handleApiResponse(response, "メニューの更新に失敗しました。");
  } catch (err) {
    handleApiError(err);
  }
};

export const deleteMenu = async (auth, data) => {
  const { access_token, token_type } = auth;
  try {
    const response = await axios.post(`${API_USER}/menu/delete`, data, {
      headers: {
        Authorization: `${token_type} ${access_token}`,
      },
    });
    return handleApiResponse(response, "メニューの削除に失敗しました。");
  } catch (err) {
    handleApiError(err);
  }
};
